@import "common";

.recomSlide, 
.level,
.kakaoMap{
	text-align: center;
	width: 100%;
	#map.web-shadow{
		width:100%;
		height: 600px;
	}
}

.sectionTitle{
	margin-top: $unit-5;
	text-align: center;
}
.subTitle{
	margin: $unit-1 0 $unit-3;
}

.bannerWrap{
  background-color: #fff;
  border-radius: 10px;
  padding: 60px 96px;
  box-sizing: border-box;
  display: flex;
  img{
    width: 504px;
    height: 282px;
    border-radius: 10px;
    object-fit:cover;
  }
  .img2{
  object-fit: none;
  }
  .bannerTitle {
    width: 50%;
    ul{
    display: flex;
    gap:10px;
    line-height: 20px;
    }
    li{
    border-radius: 50px;
    border: 1px solid $point-2;
    padding: 0 10px;
	color: $point-2;
    font-size: $mb-tt;
    }
    .youtubeBtn{
      margin-top:50px;
    //   padding: 11px;
    }
  }
  
  .bannerMainTitle{
    padding: $unit-s-4 0; 
    h1{
      font-size: $font-40;
      color: $point-1;
    }
    p{
      font-weight: bold;
      display: inline;
    }
  }
  
  .bannerSubTitle{
    width:100%;
    box-sizing: border-box;
    h3{
      font-weight: 400;
    }
    p{
      padding-top: $unit-s-4;
      color: $gray-2;
    }
  }
}


// HealthLevel

.levels{
	width:100%;
	height:50%;
	margin: 72px auto;
	gap:50px;
}

.level{
	height:100%;
	width:80%;
	margin: 0 auto;
	gap:5%;
	.levelIcon{
		img{
			width: 50%;
		}
		h2{
			color:$point-2;
			font-size:$font-20;
			font-weight: bold;
			margin: 17px 0;
		}
		button{
			margin-top: 17px;
			&.active{
				background-color: $point-2;
				color:#fff;
			}
		}
	}
}


.sectionTitle{
	color: $point-1;
	font-weight: bold;
}

.LevelCont{
	// display: none;
	width: 100%;
	margin-top: 96px;
	.levelContWrap {
		flex-wrap: wrap;
		.levelWrap{
		 width: 423px;
		 height: 380px;
		 .levelImg{
			 img{
				border-radius: 10px;
				object-fit: cover;
				width:100%;
				height: 237px;
			 }
			}
			h3{
				font-weight: 500;
				padding: 10px 0;
				cursor: pointer;
			}
			p{
				text-align: left;
				margin-bottom: 10px;
				font-size: $web-sm-tt;
				color: $gray-3;
			} 
		}
		.youtubeProfile{
		line-height: 24px;
			img{
			 width:24px;
			 height: 24px;
			 border-radius: 50%;
			 padding-right: 5px;
		 }
		}

	}
	.MoreBtn{
		width: 100%;
		button{
			width: 30%;
			height: 27px;
			font-size: 14px;
			font-weight: bold;
			border: 1px solid lightgray;
			color: gray;
			margin: 0 auto;
			display: block;
		}

	}
}


// slide
#app {
  height: 100%;
}
html,
body {
  position: relative;
  height: 100%;
}

body {
  background: #eee;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
}

.healthSlides{
	.swiper {
		width: 100%;
		height: 550px;
		margin: 0px auto;
		.swiper-wrapper{
			margin-top: 30px;
			height: 435px;
			.swiper-slide {
				height: 100%;
				text-align: center;
				font-size: 18px;
				background: #fff;
				display: flex;
				justify-content: center;
				align-items: center;
				border-radius:10px;
				img{
					display: block;
					width: 55%;
					height: 100%;
					border-radius: 50%;
					margin: 0 auto;
				}
				&:hover{
					transform: translateY(-30px);
  				transition: transform .8s;
					box-shadow: 0px 8px 35px 0px rgba(0, 0, 0, 0.16);
				}
			}
		}
	}

.recomSlide{
	.recomImg{
		width:100%;
		height: 50%;
	}
	.recomInfo{
		padding: 10px 0 20px;
	}
	h2{
		font-weight: 400;
	}
	h3{
		font-size: $web-sm-tt;
		padding: 10px 0;
	}
	p{
		font-size: $web-sm-tt;
		color:$gray-2
	}
}
	
	.append-buttons {
		text-align: center;
		margin-top: 20px;
	}
	
	.append-buttons button {
		display: inline-block;
		cursor: pointer;
		border: 1px solid #007aff;
		color: #007aff;
		text-decoration: none;
		padding: 4px 10px;
		border-radius: 4px;
		margin: 0 10px;
		font-size: 13px;
	}
}


//map
.kakaoMap{
	width:100%;
	form{
		width:30%;
		height: 35px;
		margin: 0 auto $unit-3;

		input{
			width: 90%;
			height: 100%;
			border-radius: 30px;
			border: 1px solid green;
			padding: 0 35px;
			box-sizing: border-box;
			text-align: center;	
		}
		button{
			width:10%;
			height: 100%;
			vertical-align: middle;
		}
	}
	#map{
		border-radius: 10px;
	}
	

}

/* force scrollbar */
html { overflow-y: scroll; }

body { font-family: sans-serif; }

/* ---- grid ---- */
.grid {
	margin-top: $unit-3;
	display: flex;
	flex-wrap: wrap;
	gap:20px;
	width:100%;
	height: 100%;
	.grid-item {
		width: 32%;
		height: 400px;
		border-radius: 10px;
		overflow: hidden;
		img {
		width: 100%;
		height: 100%;
		}
		&:hover{
			.grid-text{
				transform: translateY(-101%);
				transition: transform .5s;
			}
		}
	}
	.grid-text{
		transform: translateY(0%);
		width: 100%;
		height: 100%;
		color:#fff;
		background:linear-gradient(to top,rgba(0,0,0,.7),transparent);
		align-items: end;
		.text{
			height: 50px;
			padding:0 30px;
			gap:10px;
			line-height: 27px;
		}	
		img{
			width:24px;
			height:24px;
			border-radius: 50%;
			object-fit: cover;
			padding-right: 5px;
		}
	}
}

//Healthmodal
.healthModalBack{
  position: fixed;
  top:0; left: 0; bottom: 0; right: 0;
  background: rgba(0, 0, 0, 0.5);
	z-index: 100;
}

.healthModal{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
	width: 600px;
	height: 675px;
	background-color: #fff;
	border-radius: 10px;
	padding: 50px;
	box-sizing: border-box;
	z-index: 101;
	.modalText{
		height: 30px;
		align-items: center;
		justify-content: space-between;
		.text{
			img{
				width:30px;
				height: 100%;
				border-radius: 50%;
				object-fit: cover;
			}
		}
		p{
		line-height: 30px;
		padding: 0 5px;
		font-size: $web-sm-tt;
		}
	}
	.modalImg{
		width:100%;
		margin-right: 30px;
		object-fit: cover;
			img{
				width:100%;
				height:333px;
			}
		}
	.modalMainText{
		height:100px;
	}
	.shotHeartIcon{
		align-items: center;
		gap:5px;
		padding: 5px 0 15px;
		span{
			color:$gray-3;
			svg{
				width:16px;
				height: 16px;
			}
		}
	}
	.shotTags{
		flex-wrap: wrap;
		gap:3px;
		.m-badge{
			margin-right: 5px;
		}
	}
	.modalBtn{
		// display:inline-block;
		width: 12px;
		height: 12px;
		position: fixed;
		border-radius: 50%;
		background-color: #94dfb6;
		top:20px;
		right: 20px;
	}		
	}




// 카카오
.map_wrap {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
  }
  
  .map_wrap,
  .map_wrap * {
	margin: 0;
	padding: 0;
	font-family: "Malgun Gothic", dotum, "돋움", sans-serif;
	font-size: 12px;
  }
  .map_wrap a,
  .map_wrap a:hover,
  .map_wrap a:active {
	color: #000;
	text-decoration: none;
  }
  .map_wrap {
	position: relative;
	width: 100%;
	height: 500px;
  }
  #menu_wrap {
	position: absolute;
	top: 0;
	bottom: 0;
	width: 250px;
	margin: 10px 560px 30px 10px;
	padding: 5px;
	overflow-y: auto;
	background: rgba(255, 255, 255, 0.7);
	z-index: 1;
	font-size: 12px;
	border-radius: 10px;
  }
  .bg_white {
	background: #fff;
  }
  #menu_wrap hr {
	display: block;
	height: 1px;
	border: 0;
	border-top: 2px solid #5f5f5f;
	margin: 3px 0;
  }
  #menu_wrap .option {
	text-align: center;
  }
  #menu_wrap .option p {
	margin: 10px 0;
  }
  #menu_wrap .option button {
	margin-left: 5px;
  }
  #placesList li {
	list-style: none;
  }
  #placesList .item {
	position: relative;
	border-bottom: 1px solid #888;
	overflow: hidden;
	cursor: pointer;
	min-height: 65px;
  }
  #placesList .item span {
	display: block;
	margin-top: 4px;
  }
  #placesList .item h5,
  #placesList .item .info {
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
  }
  #placesList .item .info {
	padding: 10px 0 10px 55px;
  }
  #placesList .info {
	color: black;
  }
  #placesList .info .gray {
	color: #8a8a8a;
  }
